        </p>
    </header>
    <main>
        <div class="top">
            <div>
                <span>1</span>
                <p>验证手机号</p>
            </div>
            <div class="dian">·····················</div>
            <div>
                <span>2</span>
                <p>填写账号信息</p>
            </div>
            <div class="dian">·····················</div>
            <div>
                <span>3</span>
                <p>注册成功</p>
            </div>
        </div>
        <div class="main">
            <a href="../js/interface/reg.php"></a>
            <form>
                <div>
                    <span>用户名</span>
                    <input @change="filterU" type="text" id="username" placeholder="您的账户名和登录名,至少四位" v-model.lazy="username">
                </div>

                <div>
                    <span>请输入手机号</span>
                    <input @change="filterT" type="text" id="phone" placeholder="建议使用常用手机号" v-model.lazy="phone">
                </div>
                <div>
                    <span>手机验证码</span>
                    <input type="text" id="phonetest" placeholder="请输入手机验证码" value="">
                    <button>获取验证码</button>
                </div>


                <div>
                    <span>设置密码</span>
                    <input @change="filterP" type="password" id="password" placeholder="4-15位,数字字母下划线组合" v-model.lazy="password">
                </div>
                <div>
                    <span>确认密码</span>
                    <input @change="filterP1" type="password" id="password1" placeholder="请再次输入密码" v-model.lazy="password1">
                </div>


                <div>
                    <span>邮箱验证</span>
                    <input @change="filterE" type="email"  id="email" placeholder="请输入邮箱" v-model.lazy="email">
                </div>
                <div>
                    <span>邮箱验证码</span>
                    <input type="text"  id="emailtest" placeholder="请输入邮箱验证码" value="">
                    <button class="yzm">获取验证码</button>
                </div>

                <a href=""></a>
                <div>
                    <input type="button" id="submit" value="注册" @click="regG" :disabled='changeD()'>
                </div>
            </form>
        </div>
        <div class=" bottom">
            <a href=""><i></i> 企业用户注册 </a>
            <a href=""><i></i> 海外用户注册 </a>
