<script>
export default {
  name:'Post',
  data() {
    return {
      phone:'',
      username:'',
      password:'',
      password1:'',
      email:'',
      u:false,
      t:false,
      p:false,
      p1:false,
      e:false,
    }
  },
  computed: {
    url(){
      return `username=${this.username}&phone=${this.phone}&password=${this.username}&email=${this.email}`
    }
  },
  methods: {
    regG(){
      this.axios.post('http://localhost:80/api/reg.php?'+this.url)
      .then(res=>{
          alert(res.data)
          if(res.data == '注册成功'){
            this.$router.push({
              name:'denglu'
            })
          }
        })
      .catch(err=>console.log(err.message))
    },
    filterU(){
      let reg = /^[a-zA-Z0-9_-]{4,16}$/;
      if(!reg.test(this.username)){
        alert('用户名格式不正确');
        this.u = false;
      }else{
        this.u = true;
      }
    },
    filterT(){
      let reg = /^(?:(?:\+|00)86)?1\d{10}$/;
      if(!reg.test(this.phone)){
        alert('手机号格式不正确');
        this.t = false;
      }else{
        this.t = true;
      }
    },
    filterP(){
      let reg = /^[a-zA-Z]\w{4,15}$/;
      if(!reg.test(this.password)){
        alert('密码格式不正确');
        this.p = false;
      }else{
        this.p = true;
      }
    },
    filterP1(){
      if(this.password1 !== this.password){
        alert('两次密码不一致');
        this.p1 = false;
      }else{
        this.p1 = true;
      }
    },
    filterE(){
      let reg = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if(!reg.test(this.email)){
        alert('邮箱格式不正确');
        this.e = false;
      }else{
        this.e = true;
      }
    },
    changeD(){
      if(this.t+this.p+this.p1+this.u+this.e == 5){
        return false
      }else{
        return true
      }
    }
  },
}
</script>

